import { tmsPosList } from './pos_info.js'
function addRows(hot, data, row, type) {
  if (row == -1) { //通过点击新建按钮进入的方法，默认在最后一行添加
    row = 0; /* 在第一行添加新增行 */
  }
  hot.alter('insert_row', row);
  setTimeout(function(){
    hot.view.wt.wtTable.draw();//为了让新加行渲染正确
  })
  // setTimeout(function(){
  //   hot.render();//为了让新加行渲染正确
  // });
  reRenderSelectOption(hot, data, type, row, true);
}


function reRenderSelectOption(hot, data, type, newRow, isnew) { //重新渲染selectOption,让每个option
  let cellMeta = hot.getCellMetaAtRow(0);
  let rows = data.length - 1;
 
  cellMeta.forEach(item => {
    if (item.prop == "t_name") { //组织
      if (isnew) { //新建行的时候的遍历 一条数据
        hot.setCellMeta(newRow,item.col,"readOnly",false);//第一条的组织要可编辑
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].t_sources })
      } else {
        for (let i = 0; i < rows; i++) { //第一次创建hot时候的遍历 全部数据
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].t_sources })
        }
      }
      return
    }
    if (item.prop == "mac_me") { //机组
      if (isnew) { //新建行的时候的遍历
        hot.setCellMeta(newRow,item.col,"readOnly",false);//第一条的机组要可编辑
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].macName_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].macName_sources })
        }
      }
      return
    }
    if (item.prop == "dgm_name") { //采集器
      if (isnew) { //新建行的时候的遍历
        hot.setCellMeta(newRow,item.col,"readOnly",false);//第一条的采集器要可编辑
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].dgm_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].dgm_sources })
        }
      }
      return
    }
    if (item.prop == "ch_name") { //通道
      if (isnew) { //新建行的时候的遍历
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].ch_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].ch_sources })
        }
      }
      return
    }
    if (item.prop == "task_id_val") { //任务标识
      if (isnew) { //新建行的时候的遍历
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].task_id_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].task_id_sources })
        }
      }
      return
    }
    if (item.prop == "baseInfo.frequency_val") { //分析频率
      if (isnew) { //新建行的时候的遍历
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.frequency_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.frequency_sources })
          // hot.setCellMeta(i,item.col,"selectOptions",row=>{ return  ['12.5','14',4.4]})
        }
      }
      return
    }
    if (item.prop == "baseInfo.cc_mod_id_val") { //关联组态
      if (isnew) { //新建行的时候的遍历
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.cc_mod_id_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.cc_mod_id_sources })
        }
      }
      return
    }
    if (item.prop == "baseInfo.spectral") { //谱线数
      if (isnew) { //新建行的时候的遍历
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.spectral_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.spectral_sources })
        }
      }
      return
    }

     /* 转速通道 */
    if (type == 1) {
      if (item.col >= 8) {//最大最小转速
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].ch_type == 13) {
            hot.setCellMeta(i,item.col,"readOnly",false);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",true);
           }
        }
      }
       /* tms转速比禁止修改且置空 */
      if (item.prop=='baseInfo.speedRate') {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].dgm_type == 7) {
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
      }
    }
     /* 振动测点 */
     if (type == 3) {
       if (item.col >= 8) {//WL9100 屏蔽分析频率后面的所有选项
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
              if (data[rowNum].dgm_type == 10) {
                hot.setCellMeta(i,item.col,"readOnly",true);
              }
              else{
                hot.setCellMeta(i,item.col,"readOnly",false);
                if(item.col == 10 || item.col == 11){
                  hot.setCellMeta(i,item.col,"readOnly",true);
                }
                if (item.col >= 13) {//MHD 屏蔽频段
                  if (data[rowNum].dgm_type == 4) {
                    hot.setCellMeta(i,item.col,"readOnly",true);
                  }
                }
             }
          }
        }
     }
     /* 数字量 */
     if (type == 5) {
       /* V1数字量，计算方式报警禁止修改且置空 */
       if (item.col == 7 || item.col == 8 || item.col == 9) {
        for (let i = 0; i < rows; i++) {
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].dgm_type == 1 || data[rowNum].dgm_type == 2) {
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
       }
     }
     /* 晃度 和倾角测点测点 */
     if (type == 6 || type == 12) {
       if (item.col == 3 || item.col == 4 || item.col == 5) { //晃度和倾角的通道禁止编辑 是否有效 测点名称
          for (let i = 0; i < rows; i++) { 
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
       }
       //MHD频段禁止编辑
       if (item.col  >= 14) {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if(data[rowNum].dgm_type == 4){
            hot.setCellMeta(i,item.col,"readOnly",true);
          }else{
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
      }
     }
     /* 倾覆 */
     if (type == 11) {
       //MHD频段禁止编辑
       if (item.col >= 22) {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if(data[rowNum].dgm_type == 4){
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
       }
     }
     /* 温度 */
     if (type == 7) {
       //MHD频段禁止编辑
       if (item.prop=='isalarm_val') {
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
            if (data[rowNum].dgm_type == 7) {
              hot.setCellMeta(i,item.col,"readOnly",true);
            } else {
              hot.setCellMeta(i,item.col,"readOnly",false);
            }
          }
       }
     }
     /* 轨道波磨 */
     if (type == 17) {
       //已经建好的测点通道禁止编辑
       if (item.prop=='ch_name') {
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
            if (data[rowNum].operate == 1) {
              hot.setCellMeta(i,item.col,"readOnly",false);
            } 
            else if (data[rowNum].operate == 0) {
              hot.setCellMeta(i,item.col,"readOnly",true);
            }
          }
       }
     }
     /* 合成倾角 */
     if (type == 13) {
       //18X关联通道 、23Y关联通道,13传感器类型、20X从设备号 、21过程量类型 25Y从设备号、26Y过程量类型
       if (item.col == 18 || item.col == 23||item.col == 13 || item.col == 20 || item.col == 21 || item.col == 25 || item.col == 26) {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].baseInfo.sourceType == 0) {
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
       }
       /* MHD频段禁止编辑 */
        if (item.col >= 28) {
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
            if(data[rowNum].dgm_type == 4  ){
              hot.setCellMeta(i,item.col,"readOnly",true);
            } else {
              hot.setCellMeta(i,item.col,"readOnly",false);
            }
          }
        }
     }
     /* 长采样 */
     if (type == 200) {
      for (let i = 0; i < rows; i++) { 
        let rowNum = hot.toPhysicalRow(i);
         /* 新建测点采集方式可以更改 */
        if (item.col == 4) { //
          if (data[rowNum].operate == 1) {
            hot.setCellMeta(i,item.col,"readOnly",false);
          } else if (data[rowNum].operate == 0) {
            hot.setCellMeta(i,item.col,"readOnly",true);
        }
        }
        /* 定时采集 */
        if (data[rowNum].baseInfo.capture_mode == 0) {
          if (item.col == 6) {/* 序号可以编辑 */
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
          if (item.col == 7) {/* 通道禁止编辑 */
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
        }
        /* 手动采集 */
        if (data[rowNum].baseInfo.capture_mode == 1) {
          if (item.col == 6) {/* 序号禁止编辑 */
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
          if (item.col == 7) {/* 通道可以编辑 */
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
      }
     }
     /* 脱轨检测测点 */
     if (type == 18) {
        //报警默认报警且禁止编辑
        if (item.prop=='isalarm_val') {
           for (let i = 0; i < rows; i++) { 
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
        }
     }
     /*tms的测点类型需要加上“数码管显示”字段，其他类型的不可编辑*/
     if(tmsPosList.includes(type) && item.prop == 'baseInfo.if_show_val'){
      for (let i = 0; i < rows; i++) { 
        let rowNum = hot.toPhysicalRow(i);
        if(data[rowNum].dgm_type != 7){//非tms采集器的“数码管显示”不可编辑
          hot.setCellMeta(i,item.col,"readOnly",true);
        } else {
          hot.setCellMeta(i,item.col,"readOnly",false);
        }
      }
     }
  })
}
//测点类型名称转化
function posType_name(type) {
  let postType_name = '';
  switch (parseInt(type)) {
    case 3:
      postType_name = "振动测点"
      break;
    case 4:
      postType_name = "包络测点"
      break;
    case 8:
      postType_name = "振动阶次测点"
      break;
    case 9:
      postType_name = "包络阶次测点"
      break;
    case 1:
      postType_name = "转速测点"
      break;
    case 200:
      postType_name = "长采样测点"
      break;
    case 6:
      postType_name = "晃度测点"
      break;
    case 11:
      postType_name = "倾覆测点"
      break;
    case 12:
      postType_name = "倾角测点"
      break;
    case 13:
      postType_name = "合成倾角测点"
      break;
    case 7:
      postType_name = "温度测点"
      break;
    case 2:
      postType_name = "过程量测点"
      break;
    case 5:
      postType_name = "数字量测点"
      break;
    case 10:
      postType_name = "工艺量测点"
      break;
    case 14:
      postType_name = "螺栓测点"
      break;
    case 15:
      postType_name = "冲击测点"
      break;
    case 16:
      postType_name = "冲击阶次测点"
      break;
    case 17:
      postType_name = "轨道波磨测点"
      break;
    case 18:
      postType_name = "脱轨监测测点"
      break;
  }
  return postType_name
}

/**
   * *分析频率下拉选项
   * dgm_type：采集器类型；
   * posType：测点类型；分振动系的测点和包络类型测点
   * filter：滤波器；0,1,2（包络类型测点有）
   *
   * **/
function frequencySources(position, filter) {
  let dgm_type = position.dgm_type
  let posType = position.pos_type
  let frequency_sources = {};
  if (dgm_type == 1) {//dgu2000采集器
    frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '5000': '5K', '10000': '10K' };
    if(posType == 4){//包络测点
      /* 滤波器 */
      if (filter == 0) {
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500'};
      }
      else if(filter == 1){
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K'};
      }
      else if(filter == 2){
        frequency_sources = {'50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '5000': '5K', '10000': '10K' };
      }
    }
  }
  if (dgm_type == 2) {
    frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K'};
  }
  else if (dgm_type == 3 || dgm_type == 7 || dgm_type == 9 || dgm_type == 10 || dgm_type == 13) {//v2和tms、DGU2000E、WL9100、YHC5000
    if (posType == 3 || posType == 6 || posType == 11 || posType == 12 || posType == 13 || posType == 15) {//振动系测点,冲击测点
      frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K', '8000': '8K', '16000': '16K', '32000': '32K' };
      if(dgm_type == 3){//dgu2000v2新增5k,10k，满足明阳现场的需求
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K', '5000': '5K', '8000': '8K', '10000':'10k', '16000': '16K', '32000': '32K' };
      }
      /*金风现场，采集器v2和DGU2000E的定制版*/
      if(config.company===1 && (dgm_type == 3 || dgm_type == 9)){
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K', '8000': '8K', '10000':'10k','16000': '16K', '32000': '32K' };
      }
    }
    else if (posType == 4) {//包络测点,
      /* 滤波器 */
      if (filter == 0) {
        frequency_sources = { '12': '12.5', '25': '25', '50': '50', '100': '100', '200': '200' };
      }
      else if (filter == 1) {
        frequency_sources = { '12': '12.5', '25': '25', '50': '50', '100': '100', '200': '200', '400': '400', '800': '800', '1600': '1.6K', '3200': '3.2K' };
      }
      else if (filter == 2) {
        frequency_sources = { '12': '12.5', '25': '25', '50': '50', '100': '100', '200': '200', '400': '400', '800': '800', '1600': '1.6K', '3200': '3.2K', '6400': '6.4K', '12800': '12.8K' };
      }
    } else if (posType == 8 || posType == 16 || posType == 9) {//周期数
      frequency_sources = { '1': '1', '2': '2', '4': '4', '8': '8', '16': '16', '32': '32', '64': '64', '128': '128', '256': '256', '512': '512', '1024': '1024' }
    }
  }
  else if (dgm_type == 4) {
    frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K' };
  }
  if (posType == 13 && position.baseInfo.sourceType == 5) {/* 串口时，合成倾角的分析频率只有20 */
    frequency_sources = { '20': '20' }
  }
  if (posType == 12 && dgm_type == 0) {/* 串口时默认采集器的倾角的分析频率暂时写死为20 */
    frequency_sources = { '20': '20' }
  }
  return frequency_sources;
}

/**
   * *谱线数下拉选项
   * dgm_type：采集器类型；
   *
   * **/
function spectralSources(dgm_type, pos_type) {
  let spectral_source = [];
  if (dgm_type == 3 || dgm_type == 7  || dgm_type == 9 || dgm_type == 10 || dgm_type == 13) {
    if (pos_type == 8 || pos_type == 9 || pos_type == 16) {
      spectral_source = ['32', '64', '128', '256', '512', '1024', '2048']
    } else {
      spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200'];
      /* 金风现场谱线数定制*/
      if(config.company===1){
        spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '19200','25600', '51200','102400','204800','409600'];
      }
    }
  }
  else if (dgm_type == 4) {
    spectral_source = ['100', '200', '400', '800', '1600', '3200']
  }
  else if (dgm_type == 1){
    spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400'];
    if(pos_type == 13){//v1采集器合成倾角谱线数屏蔽100/200
      spectral_source = ['400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400'];
    }
  }
  else if(Number(dgm_type) === 0){
    if(pos_type == 13){//外部集成采集器合成倾角谱线数屏蔽100/200
      spectral_source = ['400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400'];
    }
    else{
      spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400']
    }
  }
  else {
    spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400']
  }
  return spectral_source;
}
/* 计算采样周期和谱线分辨率 */
function computed__frequency_spectral(position) {
  /* 当是叶轮采集器 */
  let frequency, spectral
  if (position.dgm_type == 2) {
    frequency = position.baseInfo.frequency * 2.5;
    spectral = position.baseInfo.spectral * 2.56;
  } else {
    frequency = position.baseInfo.frequency
    spectral = position.baseInfo.spectral
  }
  position.baseInfo.order = (spectral / frequency).toFixed(2) + "s";
  position.baseInfo.specOrder = (frequency / spectral).toFixed(4) + "Hz";
}

/**
 * *任务标识转换
 * **/
function taskId(val) {
  let taskIdVal = "";
  switch (parseInt(val)) {
    case 0:
      taskIdVal = "任务1";
      break;
    case 1:
      taskIdVal = "任务2";
      break;
    case 2:
      taskIdVal = "任务3";
      /*金风现场的'任务3'改成'长采样'*/
      if(config.company===1){
        taskIdVal = "长采样";
      }
      break;
  }
  return taskIdVal;
}
/* 将数字任务数组除去空元素，并排序，并转化为文字数组 */
function taskArray(arr) {
  if (!Array.isArray(arr) || arr.length == 0) {
    return []
  } else {
    let result = []
    arr.sort()
    arr.forEach(el => {
      result.push(taskId(parseInt(el)))
    })
    return result
  }
}


export { posType_name, frequencySources, spectralSources, taskId, taskArray, computed__frequency_spectral, addRows, reRenderSelectOption }
